<template>
  <div class="mine">
    <van-nav-bar
            title="收货地址选择"
            :fixed="true"
            left-arrow
            @click-left="$router.back()"
    >
      <van-icon name="add-o" slot="right" @click.native="$router.push('/mine/receiveAddress')"/>
    </van-nav-bar>
    <van-address-list
            v-model="chosenAddressId"
            :list="list"
            @edit="onEdit"
    ></van-address-list>
    <van-button size="large" type="danger" @click="exchange">确认兑换</van-button>
    <router-view>
    </router-view>
  </div>
</template>

<script>
import { mapGetters, mapMutations } from "vuex";
import Back from "../components/Back.js";

export default {
  name: "mine",
  mixins: [Back],
  data() {
    return {
      chosenAddressId: 0,
      list: [],
      oldList: []
    };
  },
  mounted() {
    this.getAddress();
    this.$bus.on("addAddress", () => {
      this.getAddress();
    });
  },
  beforeDestroy() {
    this.$bus.off("addAddress");
  },
  computed: {
    ...mapGetters(["ADDRESS_INFO", "DETAILS_ID","INTEGRAL","GOODINTEGRAL"])
  },
  methods: {
    ...mapMutations({ saveAddressInfo: "SAVE_ADDRESS_INFO" }),
    ...mapMutations({ changeIntegral: "CHANGE_INTEGRAL" }),
    onEdit(item, index) {
      this.saveAddressInfo(this.oldList[index]);
      this.$router.push("/mine/receiveAddress");
    },
    getAddress() {
      this.$toast.loading({
        mask: true,
        message: "加载中..."
      });
      axios.get(`/api/v1/Reg/ExchangePostAddrListGet`).then(res => {
        console.log(res.data);
        this.$toast.clear();
        if (res.data.State === 1) {
          this.list = [];
          this.oldList = res.data.Data;
          res.data.Data.forEach(item => {
            this.list.push({
              id: item.ID,
              name: item.RecvUserName,
              tel: item.LinkPhone,
              address:item.Address
            });
          });
          if (this.list.length !== 0) {
            this.chosenAddressId = this.list[0].id;
          } else {
            this.chosenAddressId = 0;
          }
        }
      }).catch(e=>{
        console.log(e)
        this.$toast.clear();
      });
    },
    exchange() {
      if (this.chosenAddressId === 0) {
        this.$toast("请选择或添加一个收货地址");
        return;
      }
      this.$dialog.confirm({
        title: '兑换',
        message: '确认兑换此商品？'
      }).then(() => {
        // on confirm
        axios
          .post("/api/v1/Reg/ExchangeProdRecordSave", {
            ExchangeProdID: this.DETAILS_ID,
            PostAddrID: this.chosenAddressId
          })
          .then(res => {
            if (res.data.State === 1) {
              this.$toast("兑换成功");
              this.changeIntegral(this.INTEGRAL - this.GOODINTEGRAL)
              setTimeout(() => {
                this.$toast.clear();
                this.$router.go(-2);
              }, 500);
            } else {
              this.$toast("兑换出现问题");
            }
            console.log(res.data);
          })
          .catch(e => {
            this.$toast.clear();
            this.$toast("请检查网络连接");
          });
      }).catch(() => {
        // on cancel
      });

    }
  }
};
</script>

